<template>
  <div>
    <b-table :small="true"  :items="items" :fields="fields" :borderless="true">
      <template v-slot:cell(description)="row">
        <b-form-input class="col-left form-control form-control-sm" v-model="row.item.description" />
      </template>
      <template v-slot:cell(otherAllowance)="row">
        <b-form-input
          class="col-right form-control form-control-sm"
          type="number"
          v-model="row.item.otherAllowance"
        />
      </template>
      <template slot="bottom-row">
        <td class="row-background form-control form-control-sm">
          <h6 >
            Total
          </h6>
        </td>
        <td class="col-right row-background ">{{ (this.t2.toFixed(3)) }}</td>
      </template>
    </b-table>
    <button class="mr-1" variant="primary" @click="AddItemRow()">Add Row</button>
    <button class="mr-1" variant="danger" @click="RemoveItemRow()">Remove Row</button>
  </div>
</template>
<script>
// const items = []
export default {
  data () {
    return {
      items: [
        {
          empCode: '100',
          tranactionMonth: '07',
          otherAllowance: '200',
          description: 'test'
        },
        {
          empCode: '100',
          tranactionMonth: '07',
          otherAllowance: '201',
          description: 'test1'
        }
      ],
      fields: [
        {
          key: 'description',
          label: 'Description',
          class: 'text-left'
        },
        {
          key: 'otherAllowance',
          label: 'Other Allowance',
          class: 'text-right th.sm'
        }
      ]
    }
  },
  computed: {
    // allowanceState() {
    //   return this.name.length > 2 ? true : false;
    // }
    t1 () {
      return this.items.reduce((accum, item) => {
        return accum + parseFloat(item.otherAllowance)
      }, 0.0)
    },
    t2: function () {
      return this.items.reduce(function (a, c) {
        return a + Number(c.otherAllowance || 0)
      }, 0)
    }
  },
  methods: {
    AddItemRow: function () {
      this.items.push({ empCode: '100', tranactionMonth: '07', description: '', otherAllowance: '' })
    }
  }
}
</script>
<style scoped>
.col-left {
  text-align: left;
}
.col-right {
  text-align: right;
}
.row-background {
  background-color: darkgray
}
</style>
